import moment from 'moment'
export default {
  props: ['post','target','tag',"hideTag","hideDetail"],
  computed:{
    showDate(){
      return moment(this.post.established_time).format('YYYY[ 年 ]MM[ 月 ]DD[ 日 ]')
    },
    hashtags(){
      if (this.post.hashtag){
        return JSON.parse(this.post.hashtag)
      }
      return []
    }
  }
}



