import {mapState} from 'vuex'
export default {
  mounted(){
    if (this.mobile){
      // window.open("https://www.zashare.com.tw")
      document.location = "https://www.zashare.com.tw"
    }
  },
  computed: {
    ...mapState(['mobile'])
  }
}



