import {mapState, mapGetters, mapActions} from 'vuex'
import panel_expo2018 from '@/components/panels/panel_expo2018'
import $ from 'jquery'
export default {
  data(){
    return {
      registExpo: {},
      active: 0,
      success: false,
      // rules: {
      //   type:[
      //     {required: true, message: "請選擇種類"}
      //   ],
      //   theme:[
      //     {required: true, message: "請選擇主題"}
