import {mapState, mapGetters, mapActions} from 'vuex'
import panel_expo2018 from '@/components/panels/panel_expo2018'
export default {
  data(){
    return {
      registExpoPaid: {},
      audiences: "學齡前幼兒／國小生／國中生／高中生／自學生／大專以上學生／職場新鮮人／青壯年／家長／教育工作者／投資人／創業者／學校單位／公部門／其他".split("／"),
      active: 0,
      success: false,
      rules: {
        paid_datetime:[
          {required: true,message: "請輸入匯款時間"}
        ],
        paid_direct:[
          {required: true,message: "請選擇匯款方式"}
        ],
        paid_name:[
          {required: true,message: "請輸入戶名"}
        ],
        paid_amount: [
          {required: true,message: "請輸入匯款金額"}
        ],
        paid_last_number:[
          {required: true,message: "請輸入匯款後五碼"}
        ],
        receipt_type:[
          {required: true,message: "請選擇發票種類"}
        ],
        receipt_name:[
          {required: true,message: "請輸入收件者姓名"}
        ],
        receipt_phone:[
          {required: true,message: "請輸入收件者電話"}
        ],
        receipt_address:[
          {required: true,message: "請輸入收件者地址"}
        ],
        receipt_postcode:[
          {required: true,message: "請輸入收件者郵遞編號"}
        ],

      }
    }
  },
  computed: {
    ...mapState({
      auth: 'auth',
      user: state=>state.auth.user,
      token: state=>state.auth.token,
      registExpoOriginal: state=>state.registExpo
    })
  },
  mounted(){
    // console.log(this.user)
    // this.userClone = JSON.parse(JSON.stringify(this.user))
    this.loadRegistData()

  },
  methods: {
    ...mapActions(['loadRegistData','updateRegistForm']),
    sendRegistForm(){
      let _this = this

      this.$refs['form_registexpo_paid'].validate(valid=>{
        if (valid){

          this.$confirm('確認送出付款確認？送出將無法更改', '最後確認', {
            confirmButtonText: '確定',
            cancelButtonText: '取消',
          }).then(() => {
            this.updateRegistForm({
              data:{
                paid_record: this.registExpoPaid
              },
              callback(){
                _this.$message({
                  message: '付費紀錄更新成功',
                  type: 'success'
                });
      
                _this.active=4
              }
            })
            
          })
        }else{
          this.$message({
            message: '資料填寫不完整，請往前填寫完整後再送出',
            type: 'error'
          });          
        }
      })
    },
    prev() {
      if (this.active-- < 0) this.active = 3;
      window.scrollTo(0,0)
    }, 
    next() {
      if (this.active++ > 3) this.active = 0;
      window.scrollTo(0,0)
    }
  },
  watch: {
    registExpoOriginal(){
      let newdata = JSON.parse(JSON.stringify(this.registExpoOriginal.paid_record))
      console.log(newdata)
      this.$set(this,"registExpoPaid",newdata)
      
      if (this.registExpoPaid==null){
        console.log("init paid")
        this.registExpoPaid={}
      }
    },
    active(){
       this.$nextTick(()=>{
        _jf.flush()
      })
    }
  },
  components: {
    panel_expo2018
  }
}



