import {mapState, mapGetters} from 'vuex'
export default {
  name: 'manage_app',
  data(){
    return {
      activeIndex: '1'
    }
  },
  methods:{
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  computed: {
    ...mapGetters({
      canManage: 'auth/canManage',
      isAdmin: 'auth/isAdmin',
    }),
    ...mapState({
      auth: 'auth',
      user: state=>state.auth.user,
      mobile: state=>state.mobile
    })
  }
}



