import {mapState} from 'vuex'
import axios from 'axios'
export default {
  data(){
    return {
      // posts: [],
      keyword: "",
      activeName: "regist",
      now_year: "2016",
      registexpos: [],
      statuses: [
        {label: "甄選中", value: "selecting"},
        {label: "入選", value: "selected"},
        {label: "未入選", value: "not-selected"},
        {label: "取消", value: "cancel"},
        {label: "合作", value: "cooperation"},
        {label: "贊助", value: "sponsor"},
        {label: "其他", value: "other"},
        {label: "-", value: ""}
      ]
    }
  },
  created(){
    console.log("loag")
    this.loadAll()
  },
  methods: {
    // handleEdit(row){
    //   this.$router.push("/api/registexpo")
    // },
    getStatus(value){
      return this.statuses.find(st=>st.value==value)
    },
    filterYear(value,row){
      return row.year === value;
    },

    loadAll(){
      axios.get("/api/registexpo",{
        params: {
          token: this.token,
        }
      }).then(res=>{
        this.$set(this,"registexpos",res.data)
      })
    },
    handleClick(tab, event) {
          console.log(tab, event);
    },
    uploadPaidStatus(scope){
      console.log("upload status!!!!!!!!");
      axios.post('/api/confirmPaid',{
        id: scope.row.id,
        value: scope.row.confirmed,
        token: this.token
      }).then(res=>{
        console.log(res.data)
        if (res.data.status=="success"){
          this.loadAll()
          this.$message({
            message: '更新成功',
            type: 'success'
          }); 
        }else{
          this.$message({
            message: '更新狀態失敗',
            type: 'error'
          }); 
        }
      })

    }
        
  },
  computed:{
     ...mapState({
      catas: state=>state.manage.catas,
      token: state=>state.auth.token
    }),
    filteredRegistexpo(){
      return (this.registexpos || []).filter(regist=>{
        return this.keyword=="" || JSON.stringify(regist).indexOf(this.keyword)!=-1
      }).map(regist=>{
        let paid_record_status = "✖︎未回報"
        if (regist.paid_record && regist.paid_record.id){
          if (regist.paid_record.confirmed){
            paid_record_status="✔︎已確認"
          }else{
            paid_record_status="待確認"
          }
        }
        return {
          ...regist,
          paid_record_status
        }
      })
    },
    paidRecords(){
      return this.filteredRegistexpo.map(r=>({
        registid: r.id,
        registname: r.name_cht,
        ...r.paid_record,
        confirmed: (r.paid_record && r.paid_record.confirmed)?true:false
      }) ).filter(r=>r.id)
    },
    workshops(){
      return this.filteredRegistexpo.map(r=>({
        registid: r.id,
        registname: r.name_cht,
        ...r.regist_workshop
      })).filter(r=>r.id)
    },
    registSpeaks(){
      return this.filteredRegistexpo.map(r=>({
        registid: r.id,
        registname: r.name_cht,
        ...r.regist_expo_speak
      })).filter(r=>r.id)

    }

  }
}



